/* Container needed to position the overlay. Adjust the width as needed */
.image-container {
  position: relative;
  min-width: 200px;
  height: 300px;
  margin-bottom: 60px;
}

.image-container img { 
  /* vertical-align: middle; */
  width: 100%;
  min-height: 300px;
  flex-grow: initial;
  opacity: 0.8;
  object-fit: fill;
  border-radius: 8px;
}



/* When you mouse over the container, fade in the overlay title */
.image-container:hover .overlay {
  opacity: 1;
  cursor: pointer;
}

/* The overlay effect - lays on top of the container and over the image */
.overlay {
  position: absolute;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  /* Black see-through */
  width: 100%;
  transition: 0.5s ease;
  opacity: 0;
  color: white;
  font-size: 20px;
  padding: 15px;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.6) 0px 5px 30px;
}

.overlay-top {
  position: absolute;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  top: 0;
  background: rgba(0, 0, 0, 0.6);
  /* Black see-through */
  width: 100%;
  transition: 0.5s ease;
  opacity: 0;
  color: white;
  font-size: 20px;
  padding: 15px;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.6) 0px 5px 15px;
}

/* When you mouse over the container, fade in the overlay title */
.image-container:hover .overlay-top {
  opacity: 1;
  z-index: 6;
}

.image-container:hover img {
  opacity: 1;
  scale: 1.01;
  box-shadow: rgba(0, 0, 0, 0.6) 0px 5px 15px;
}


