.grid-2 {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 2fr));
  grid-gap: 5px;
}

.contact-container,
.form-container {
  margin-top: 30px;
  text-align: center;
}

.contact-container h4,
.form-container h4 {
  margin-bottom: 20px;
}

/* --------------------------
conact us  form section
---------------------------- */
form {
  display: inline-block;
  max-width: 700px;
  width: 100%;
  border-radius: 15px;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 45px;
  padding-bottom: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

form:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
}

#button-send {
  padding: 14px 20px;
  margin: 8px 0;
  color: white;
  border: none;
  width: 100%;
  margin-top: 10px;
  margin-bottom: 25px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

#button-send:hover {
  opacity: 0.9;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.3);
}

*:focus {
  outline: none;
  border: none;
  background-color: rgba(164, 198, 57, 0);
}

.invalid-feedback,
.valid-feedback {
  padding: 10px;
}

.input-group-text {
  background-color: rgba(164, 198, 57, 0);
  box-shadow: inset 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border: none;
  border-right: #b71c1c 1px solid;
}

*[placeholder] {
  font-size: small;
  color: white;
  background-color: rgba(164, 198, 57, 0);
  box-shadow: inset 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border: none;
  font-weight: bold;
}

*[placeholder]:focus {
  font-size: small;
  color: white;
  background-color: rgba(164, 198, 57, 0);
  box-shadow: inset 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border: none;
}

#message {
  min-height: 15px;
}
/* --------------------------
   end of contact us section
  ---------------------------- */

